:root {
  --bg-text-color: #1b1f3e;
  --logo-fade: #121528;
  --link-hover-1: #fa0043;
  --primary-color: yellow;
  --secondary-color: #e06800;
  --body-background: #0c0e1c;
  --validate-green: #00b400;
}

.global-header {
  background-color: var(--bg-text-color);
  height: 80px;
  align-items: center;
  display: flex;
}

.header_titles {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.header_logo_link {
  align-items: center;
  display: flex;
}

.header_logo-image {
  width: 55px;
  height: 45px;
  border: 5px solid var(--logo-fade);
  border-radius: 20px;
}

.header_monday_container, .header_tuesday_container, .header_wednesday_container, .header_thursday_container, .header_friday_container, .header_saturday_container, .header_sunday_container {
  width: 130px;
  height: 52px;
}

.header_link {
  width: 100%;
  height: 100%;
  color: #fff;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header_link:hover {
  color: var(--link-hover-1);
}

.header_link:before {
  content: "";
  width: 0;
  height: 3px;
  background: var(--link-hover-1);
  transition: width .3s linear .1s, transform .3s linear .1s;
  position: absolute;
  bottom: 3px;
  left: 50%;
  transform: translateX(-50%);
}

.header_link:hover:before {
  width: 130px;
  color: var(--link-hover-1);
  transform: translateX(-50%);
}

.header_titles li a span {
  color: inherit;
  font-family: Permanent Marker;
  font-size: 22px;
  font-weight: 400px;
}

